body {
    background-image: url(../img/blog/blog-page.jpg);
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.blog {
    padding-left: 293px;
    padding-bottom: 250px;

    &-bg {}

    &__inner {
        max-width: 1348px;
        margin: auto;
    }

    @include tablet {
        padding-left: 0;
    }

    @include mobile-620 {
        padding-bottom: 150px;
    }

    &__main {
        padding-right: 293px;

        @include tablet-1200 {
            padding-right: 0;
        }
    }

    &__date {
        font-style: normal;
        font-weight: normal;
        font-size: 22px;
        line-height: 140%;
        color: $gray;
    }

    &__line {
        margin-top: 79px;

        &--mt {
            &-70 {
                margin-top: 70px;
            }
        }

        @include tablet-1200 {
            margin-top: 40px;
        }
    }

    &__title {
        font-style: normal;
        font-weight: normal;
        font-size: 110px;
        line-height: 110%;
        color: $white-main;
        margin-top: 20px;

        @include tablet {
            font-size: 70px;
        }

        @include mobile {
            font-size: 50px;
        }

        @include mobile-520 {
            font-size: 40px;
        }
    }

    &__text {
        font-style: normal;
        font-weight: normal;
        font-size: 22px;
        line-height: 140%;
        color: $white-secodary;
        max-width: 840px;
        margin-top: 70px;

        @include tablet-1200 {
            margin-top: 35px;
        }

        @include mobile-620 {
            font-size: 20px;
        }

        &--big {
            font-style: normal;
            font-weight: normal;
            font-size: 40px;
            line-height: 120%;
            margin-top: 150px;
            max-width: 1134px;

            @include tablet-1200 {
                margin-top: 75px;

                @include mobile-620 {
                    margin-top: 35px;
                }
            }

            @include mobile {
                font-size: 35px;
            }

            @include mobile-520 {
                font-size: 25px;
            }

            &--mt {
                &-70 {
                    margin-top: 70px;

                    @include mobile-620 {
                        margin-top: 35px;
                    }
                }
            }
        }

        &--mt {
            &-120 {
                margin-top: 120px;

                @include tablet-1200 {
                    margin-top: 60px;
                }
            }
        }
    }

    &__img {
        width: 100%;
        object-fit: cover;

        &:first-child {
            margin-right: 36px;
        }

        &--mt {
            &-120 {
                margin-top: 120px;

                @include tablet-1200 {
                    margin-top: 60px;
                }
            }

            &-20 {
                @include mobile-620 {
                    margin-top: 20px;
                }
            }
        }

        &-wrap {
            display: flex;
            max-height: 430px;
            margin-top: 120px;

            >img {
                width: calc(49% - 13px);

                @include mobile-620 {
                    width: 100%;
                }
            }

            @include tablet-1200 {
                margin-top: 60px;
            }

            @include mobile-620 {
                flex-direction: column;
                max-height: max-content;
            }
        }
    }

    &__more {
        margin-top: 150px;

        @include tablet-1200 {
            margin-top: 75px;
        }
    }

    &-more {
        &__author {
            &-text {
                font-style: normal;
                font-weight: normal;
                font-size: 22px;
                line-height: 140%;
                color: $gray;
                margin-right: 39px;
            }

            &-name {
                font-style: normal;
                font-weight: normal;
                font-size: 22px;
                line-height: 140%;
                color: $white-main;
            }
        }

        &__share {
            display: flex;
            align-items: center;
            margin-top: 40px;

            &-text {
                font-style: normal;
                font-weight: normal;
                font-size: 22px;
                line-height: 140%;
                color: $gray;
                margin-right: 39px;
            }
        }
    }

    &-comments {
        &__item {
            margin-top: 100px;

            @include mobile-620 {
                margin-top: 50px;
            }
        }

        &__title {
            font-style: normal;
            font-weight: 300;
            font-size: 70px;
            line-height: 110%;
            color: $white-main;
            margin-top: 200px;

            @include tablet-1200 {
                margin-top: 100px;
            }

            @include mobile {
                font-size: 50px;
            }
        }

        &__subtitle {
            font-style: normal;
            font-weight: 300;
            font-size: 40px;
            line-height: 120%;
            color: $white-main;
            margin-top: 200px;

            @include tablet-1200 {
                margin-top: 100px;
            }
        }

        &__wrap {
            margin-top: 150px;

            @include tablet-1200 {
                margin-top: 75px;
            }
        }

        &__form {
            margin-top: 130px;

            @include tablet-1200 {
                margin-top: 65px;
            }
        }
    }
}

.links {
    display: flex;
    align-items: center;

    @include mobile-520 {
        flex-wrap: wrap;
        justify-content: space-around;

    }

    &__item {
        width: 46px;
        height: 46px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        background: $blue;

        &:not(:last-child) {
            margin-right: 20px;

            @include mobile-450 {
                margin-right: 10px;
            }
        }

        &:hover {
            opacity: .7;
        }

        @include mobile-450 {
            // width: 34%;
            margin-bottom: 20px;
        }
    }
}

.comment {
    display: flex;
    max-width: 1080px;
    height: 100%;
    justify-content: flex-start;

    @include mobile-620 {
        flex-direction: column;
        align-items: flex-start;
    }

    @include mobile-450 {
        position: relative;
    }

    &--reply {
        margin-left: 150px;

        @include tablet-1200 {
            margin-left: 70px;
        }

        @include mobile-620 {
            margin-left: 40px;
        }
    }

    &__avatar {
        min-width: 107px;
        height: 107px;
        object-fit: cover;
        border-radius: 50%;
        margin-right: 40px;

        @include mobile {
            min-width: 90px;
            height: 90px;
        }

        @include mobile-620 {
            margin-right: 0;
            margin-bottom: 20px;
        }
    }

    &__date {
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 140%;
        color: $gray;
    }

    &__wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__name {
        font-style: normal;
        font-weight: 300;
        font-size: 40px;
        line-height: 120%;
        color: $white-main;
        margin-top: 10px;

        @include mobile {
            font-size: 30px;
        }
    }

    &__reply {
        font-style: normal;
        font-weight: normal;
        font-size: 22px;
        line-height: 140%;
        color: $white-main;

        &:hover {
            text-decoration: underline;
        }

        @include mobile-450 {
            position: absolute;
            top: 0;
            right: 0;
        }
    }

    &__text {
        font-style: normal;
        font-weight: normal;
        font-size: 22px;
        line-height: 140%;
        color: $white-secodary;
        margin-top: 40px;

        @include mobile-520 {
            font-size: 18;
            margin-top: 20px;
        }
    }

    &-form {
        display: flex;
        flex-direction: column;
        max-width: 840px;

        &__wrap {
            display: flex;
            align-items: center;
            justify-content: space-between;

        }

        &__input {
            background: transparent;
            font-style: normal;
            font-weight: normal;
            font-size: 18px;
            line-height: 140%;
            color: $white-main;
            border-bottom: 1px solid $gray;
            max-width: 400px;
            padding-bottom: 20px;
            width: 100%;

            @include mobile-450 {
                min-width: auto;
            }

            @include tablet-1200 {
                &:first-child {
                    margin-right: 30px;
                }
            }
        }

        &__textarea {
            background: transparent;
            font-style: normal;
            font-weight: normal;
            font-size: 18px;
            line-height: 140%;
            height: 46px;
            color: $white-main;
            border-bottom: 1px solid $gray;
            padding-bottom: 20px;
            margin-top: 170px;

            @include tablet-1200 {
                margin-top: 30px;
            }
        }

        &__lable {
            font-style: normal;
            font-weight: normal;
            font-size: 18px;
            line-height: 140%;
            color: $white-main;
        }

        &__button {
            border-radius: 50%;
            cursor: pointer;
            margin-top: 70px;
            align-self: flex-end;
            transition: .3s ease;
            transform: rotate(0deg);

            &:hover {
                transform: rotate(-90deg);
            }
        }

        &__checkbox {
            &-wrap {
                margin-top: 70px;
            }
        }
    }
}